
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

export default {
name: "subStoreList",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading
},
data() {
return {
password: '',



showSetting:true,
showSuccess:false
};
},
created() {

},
mounted() {
this.$nextTick(function() {});
},
methods: {

bindPwd(e){
var password=this.password;
if(password.length>5){
this.password=password.slice(0,6);
}
},
async setPwd(){
var param={
plat: 3,
token:this.token,
account:this.account,
pwd: this.$encrypt(this.password)
};
const [err, res] = await api.setTradePassword(param);
if(err){
this.$vux.toast.text(err.msg);
return;
}else{
if(res.code==2000){
this.showSuccess=true;
this.showSetting=false;
}
}

},
linkToPage(){
this.$router.push('../../index/index');
},


},
filters: {},
computed: {
...mapGetters(["user", "account", "token"])
}
};



